<template>
  <div class="">
    <h2 class="page-title">صور الصفحة الرئيسية</h2>
    <br />
    <!-- Gallery -->
    <div class="container">
      <div class="gallery row justify-content-center">
        <div v-if="images.length == 0" class="center">
          <h3>No images</h3>
        </div>
        <div v-for="(img, i) in images" :key="i" class="col-lg-4 col-md-12 mb-4">
          <div class="img">
            <i @click="deleteImage(img._id, img.number)" class="fa fa-times"></i>
            <img
              :src="img.url || placeholder"
              class="w-100 shadow-1-strong rounded mb-4"
              alt="Boat on Calm Water"
            />
          </div>
        </div>
      </div>
    </div>
    <!-- Gallery -->
    <!-- UPLOAD -->
    <br />
    <hr />
    <div v-if="images.length < 21" class="container">
      <div class="subtitle">أضافة صورة جديدة</div>
      <div class="upload">
        <div class="center">
          <div class="img">
            <input style="display: none" type="file" ref="pick" @change="pickImage" />
            <i @click="remove" class="remove fa fa-times"></i>
            <img
              :src="newImage.url"
              alt="new-image"
              class="img-fluid"
              @click="
                () => {
                  remove;
                  $refs.pick.click();
                }
              "
            />
            <div v-if="loading" class="loading">
              <loading></loading>
            </div>
          </div>
        </div>
        <hr />
        <div class="row justify-content-center align-items-center" dir="ltr">
          <div class="col-md-4">
            <div class="form-group">
              <label>ترتيب الصورة</label>
              <input
                type="number"
                class="form-control"
                v-model.number="newImage.number"
              />
            </div>
          </div>
          <div class="col-md-4">
            <button @click="upload" :disabled="loading || !image" class="btn btn-success">
              upload
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Axios from "axios";
export default {
  created() {
    this.newImage.url = this.placeholder;
    Axios.get("/getGallery").then((res) => {
      if (res.data.err) return alert(res.data.err);
      if (res.data.images.length == 0) return;
      this.images = res.data.images;
    });
  },
  data() {
    return {
      images: [],
      loading: false,
      placeholder:
        "https://media.istockphoto.com/vectors/thumbnail-image-vector-graphic-vector-id1147544807?k=20&m=1147544807&s=612x612&w=0&h=pBhz1dkwsCMq37Udtp9sfxbjaMl27JUapoyYpQm0anc=",
      newImage: {
        url: null,
        number: 1,
      },
      image: null,
    };
  },
  methods: {
    getSrc(i) {
      const image = this.images[i];
      if (image && image != "" && image != "null" && image != "undefined") return image;
      else return "";
    },
    pickImage(e) {
      const file = e.target.files[0];
      console.log(file);
      if (!["png", "jpg", "jpeg", "svg"].includes(file.type.split("/")[1]))
        return alert("صورة غير صالحة");
      if (file.size > 10000000) return alert("الحجم المسموح به حتى 10 ميجا");
      this.newImage.url = URL.createObjectURL(file);
      this.image = file;
    },
    remove() {
      this.newImage = { url: this.placeholder, number: 1 };
      this.image = null;
    },
    upload() {
      const formData = new FormData();
      formData.append("number", this.newImage.number);
      formData.append("image", this.image);
      this.loading = true;
      Axios.post("/uploadGalleryImage", formData).then((res) => {
        this.loading = false;
        this.remove();
        this.images = res.data.images;
        document.getElementById("gallery").scrollIntoView({ behavior: "smooth" });
      });
    },
    deleteImage(id, number) {
      if (!confirm("مسح الصورة؟")) return;
      Axios.delete(`/deleteGalleryImage/${id}/${number}`).then((res) => {
        if (res.data.err) return alert(res.data.err);
        const index = this.images.findIndex((img) => img._id == id);
        this.images.splice(index, 1);
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.upload {
  .img {
    position: relative;
    width: 100%;
    height: 300px;
    width: 300px;
    border: 2px dotted #111;
    border-radius: 10px;
    cursor: pointer;
    height: 300px;
    img {
      width: 100%;
      height: 100%;
    }
    .loading {
      position: absolute;
      left: 35%;
      top: 35%;
    }
    .remove {
      position: absolute;
      font-size: 2rem;
      top: 1%;
      right: -11%;
    }
  }
}
.gallery {
  .img {
    width: 300px;
    height: 300px;
    position: relative;
    border: 3px solid rgba(36, 37, 41, 0.648);
    border-radius: 10px;
    img {
      width: 100%;
      height: 100%;
    }
    i {
      position: absolute;
      font-size: 2rem;
      top: 1%;
      right: -11%;
    }
  }
}

hr {
  height: 5px;
}
</style>
